.badge {
  border-radius: 16px;
  font-size: 16px;
  font-weight: 400;
  padding: 7px 16px;
  display: inline-block;
  outline: 0;
  border: 0;

  &.full-width {
    width: 100%;
  }

  &.dark {
    color: white;
  }

  &.success.dark {
    background: #256700;
  }

  &.success.light {
    background: #F8F8F9;
  }

  &.success.light, &.success.bg-white, &.success.bg-none {
    color: #256700;
  }

  &.warning.dark {
    background: #FDB81E;
    color: #40454E;
  }

  &.warning.light {
    background: #FFFAEF;
    color: #40454E;
  }

  &.warning.light, &.warning.bg-white, &.warning.bg-none {
    color: #40454E;
  }

  &.error.dark {
    background: #E4292F;
  }

  &.error.light {
    background: #F8F8F9;
  }

  &.error.light, &.error.bg-white, &.error.bg-none {
    color: #D8171B;
  }

  &.info.dark {
    background: #14568D;
  }

  &.info.light {
    background: #EDF8FD;
  }

  &.info.light, &.info.bg-white, &.info.bg-none {
    color: #14568D;
  }

  &.neutral.dark {
    background: #515761;
  }

  &.neutral.light {
    background: #EDEEEF;
  }

  &.bg-white {
    background: white !important;
  }

  &.bg-none {
    background: transparent !important;
  }


  &.tag, &.chip {
    background: #DEF2FF;
    border: 1px solid rgba(0,0,0,.05);
    color: #515761;
    transition: all 0.2s;

    &:focus, &:hover {
      cursor: pointer;
    }

    &:hover {
      background-color: darken(#DEF2FF, 20%);
    }

    &:focus {
      box-shadow: 0px 0px 0px 2px #30588c, inset 0px 0px 0px 2px white;
      border: 1px solid transparent;
    }

    &.action {
      color: #14568D;
      text-decoration: underline;
    }

    .close {
      border: 0;
      background: transparent;
      background-repeat: no-repeat;
      margin-left: 16px;
      top: -2px;
      position: relative;
      outline: 0;
      width: 12px;
      height: 12px;
