.fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

.form-item {
  margin-bottom: 1.5rem;
  display: flex;
  flex-direction: column;
  flex: 1;
  align-items: flex-start;
}

.label {
  font-weight: 500;
  color: #515761;
  display: inline-block;
  vertical-align: baseline;
  margin-bottom: 8px;
  padding: 0;
}

.input-group {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  width: 100%;
}

.input-group > input {
  font-size: 1000px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.input-group-append {
  display: flex;
  margin-left: 0;
  z-index: 100;
}
.input-group-append > button {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

@import "components/forms/checkbox";
@import "components/forms/textfield";
@import "components/forms/select";
@import "components/forms/radiobutton";
@import "components/forms/search";
