.switch {
  position: relative;
  display: inline-block;
  width: 70px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #F8F8F9;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 34px;
  border: 1px solid #14568D;
}

.slider:before {
  position: absolute;
  content: "";
  height: 28px;
  width: 28px;
  left: 1px;
  bottom: 1px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%;
  border: 1px solid #515761;
}

input:checked + .slider {
  background-color: rgba(20,86,141,0.7);
}

input:focus + .slider {
  box-shadow: 0px 0px 0px 2px white, 0px 0px 0px 4px #14568D;
}

input:checked + .slider:before {
  transform: translateX(36px);
}
