.table {
  width: 100%;
  margin-bottom: 1rem;
  color: #515761;
  border-collapse: collapse;

  thead th {
    border-bottom: 1px solid #515761;
    color: #515761;
  }

  td, th {
    padding: 0 16px;
    height: 56px;
    vertical-align: middle;
    text-align: inherit;
  }

  td {
    border-top: 1px solid #B9BCC0;
  }

  tbody tr:hover {
    color: #212529;
    background-color: #DDF2FF;
  }

  &.table-striped tbody tr:nth-of-type(odd) {
    background-color: #ECECEC;
  }
}
