.accordion-wrapper {
  .accordion:first-child .accordion__title {
    border-top: 1px solid #B9BCC0;
  }
}

.accordion {

  .accordion__title {
    cursor: pointer;
    width: 100%;
    text-align: left;
    outline: none;
    transition: box-shadow, color 0.4s;
    background: transparent;
    border-left: 0;
    border-right: 0;
    border-top: 1px solid transparent;
    border-bottom: 1px solid #B9BCC0;
    border-left: 1px solid transparent;
    border-right: 1px solid transparent;
    line-height: 32px;
    padding: 7px 16px;

    color: #009BDF;
    font-size: 24px;
    font-weight: 300;

    &:focus {
      border: 1px solid transparent !important;
      box-shadow: 0 0 0 2px #4378A4;
    }

    &:hover {
      color: #14568D;
    }

