.link {
  transition: all 0.2s;
  color: #007c89;
}
.link:focus {
  box-shadow: 0px 0px 0px 2px #14568D;
  outline: 0;
  border-radius: 3px;
}

.link.underline {
  text-decoration: underline;
}

.link.accredo.light:focus {
  box-shadow: 0px 0px 0px 2px #fff;
}
.link.accredo:focus {
  box-shadow: 0px 0px 0px 2px #007c89;
}
.link.accredo:hover {
  color: darken(#007c89, 20%);
}
