ul.breadcrumb {
  padding: 8px 16px;
  list-style: none;
}

ul.breadcrumb li {
  display: inline;
  font-weight: 500;
}

ul.breadcrumb li+li:before {
  padding: 8px;
  color: #515761;
  content: "â€¢";
}

ul.breadcrumb li a {
  color: #14568D;
  font-weight: 400;

  &:hover {
    text-decoration: none;
  }

  &:focus {
    box-shadow: 0px 0px 0px 2px #14568D;
    outline: 0;
    border-radius: 3px;
  }
}
