.pagination {
  margin: 0;
  padding: 0;
}

.pagination li {
  display: inline-block;
  text-align: center;
  list-style-type: none;

  &.active {
    background-color: #14568D;

    a {
      color: #fff;
    }
  }

  .arrow-icon {
    margin-top: 3px;
  }

  a {
    color: #14568D;
    display: table-cell;
    vertical-align: middle;
    font-size: 16px;
    height: 32px;
    width: 32px;
    text-decoration: none;
    border: 1px solid transparent;
    position: relative;

    &:focus {
      box-shadow: 0px 0px 0px 2px #fff, 0px 0px 0px 4px #14568D;
      outline: 0;
      z-index: 100;
    }

    &:hover {
      background: #F1F9FD;
      border: 1px solid #009BDF;
      color: #14568D;
    }
  }
}
