.dialog {
  width: 500px;
  overflow: auto;
  background: #F8F8F9;
  box-shadow: 0 0 5px 0 rgba(0,0,0,0.20);

  .dialog-header {
    height: 8px;
    position: relative;
  }

  &.alert .dialog-header {
    background-color: #E4292F;
  }

  &.default .dialog-header {
    height: 32px;
    background-color: #14568D;
  }

