  .close:focus {
    box-shadow: 0px 0px 0px 2px white, 0px 0px 0px 4px #515761;
    outline: 0;
  }

  &.default .close {
    top: 10px;
  }

  &.default .close:focus {
    box-shadow: 0px 0px 0px 2px #14568D, 0px 0px 0px 4px white;
    outline: 0;
  }

