  .dialog-body {
     padding: 32px;
     position: relative;
     overflow: auto;

     .dialog-body__container {
       width: 380px;
       margin-left: 16px;
       float: left;
     }

     h4 {
       font-weight: 300;
       font-size: 28px;
       color: #515761;
       letter-spacing: 0;
       line-height: 32px;
       margin: 0 0 8px;
       padding: 0;
     }

     p {
       font-weight: 300;
       font-size: 20px;
       color: #515761;
       letter-spacing: 0;
       line-height: 24px;
       padding: 0;
       margin: 0;
     }
  }

  .dialog-footer {
    text-align: center;
    padding: 16px;
    background: white;
    clear: both;
  }
}
