/* Buttons */
.btn {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: 0;
  cursor: pointer;
  font-size: 19px;
  font-weight: 500;
  padding: 14px 24px;
  text-align: center;
  text-decoration: none;
  transition-duration: 0.15s;
  transition-timing-function: ease-in;
  line-height: 16px;
  border: 0;
  border-radius: 4px;

  &.full-width {
    width: 100%;
  }
}

.btn:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

.btn.small {
  font-size: 16px;
  padding: 8px 16px;
}

.btn.esi {
  color: #fff;
  background: #009BDF;
  background-image: linear-gradient(-180deg, rgba(255,255,255,0.10) 0%, rgba(0,0,0,0.10) 100%);
}

.btn.esi:focus,
.btn.esi:hover:enabled,
.btn.esi.focus, .btn.esi.hover {
  background-color: #30588c;
}

.btn.esi:focus,
.btn.esi.focus {
  outline: 0;
  box-shadow: 0px 0px 0px 2px white, 0px 0px 0px 4px #14568D;
}

.btn.esi.additional:focus,
.btn.esi.additional.focus {
  outline: 0;
  box-shadow: inset 0px 0px 0px 2px #30588c, inset 0px 0px 0px 4px white;
}

.btn.esi:active {
  background-color: #234066;
}

.btn.esi.secondary {
  color: #009BDF;
  background: #fff;
  background-image: linear-gradient(-180deg, rgba(255,255,255,0.05) 0%, rgba(0,0,0,0.05) 100%);
  border: 1px solid #009BDF;
  border-radius: 4px;
}

.btn.esi.secondary:focus,
.btn.esi.secondary:hover:enabled,
.btn.esi.secondary.focus, .btn.esi.secondary.hover {
  color: #14568D;
  background: #F7F7F7;
  border: 1px solid #14568D;
  border-radius: 4px;
}

.btn.esi.secondary:focus,
.btn.esi.secondary.focus {
  outline: 0;
  box-shadow: 0px 0px 0px 2px white, 0px 0px 0px 4px #14568D;
}

.btn.esi.secondary.additional:focus,
.btn.esi.secondary.additional.focus {
  outline: 0;
  box-shadow: inset 0px 0px 0px 2px white, inset 0px 0px 0px 3px #14568D;
}

.btn.esi.secondary:active,
.btn.esi.secondary.active{
  background: #BDE2F3;
  border: 1px solid #14568D;
  box-shadow: inset 0 2px 5px 0 rgba(1,0,0,0.20);
}

.btn.esi.tertiary {
  color: #009BDF;
  background: transparent;
}

.btn.esi.tertiary:focus,
.btn.esi.tertiary:hover:enabled,
.btn.esi.tertiary.focus, .btn.esi.tertiary.hover {
  color: #14568D;
  background-color: transparent;
}

.btn.esi.tertiary:focus,
.btn.esi.tertiary.focus {
  outline: 0;
  box-shadow: inset 0px 0px 0px 2px #14568D;
}



.btn.accredo {
  color: #fff;
  background: #15A4A7;
  background-image: linear-gradient(-180deg, rgba(255,255,255,0.10) 0%, rgba(0,0,0,0.10) 100%);
}

.btn.accredo:focus,
.btn.accredo:hover,
.btn.accredo.focus, .btn.accredo.hover {
  background: #007C89;
}

.btn.accredo:focus,
.btn.accredo.focus {
  outline: 0;
  box-shadow: 0px 0px 0px 2px white, 0px 0px 0px 4px #007C89;
}

.btn.accredo:active {
  background-color: #234066;
}







.btn.accredo.secondary {
  color: #15A4A7;
  background: #fff;
  background-image: linear-gradient(-180deg, rgba(255,255,255,0.05) 0%, rgba(0,0,0,0.05) 100%);
  border: 1px solid #15A4A7;
  border-radius: 4px;
}

.btn.accredo.secondary:focus,
.btn.accredo.secondary:hover:enabled,
.btn.accredo.secondary.focus, .btn.accredo.secondary.hover {
  color: #15A4A7;
  background: #F7F7F7;
  border: 1px solid #15A4A7;
  border-radius: 4px;
}

.btn.accredo.secondary:focus,
.btn.accredo.secondary.focus {
  outline: 0;
  box-shadow: 0px 0px 0px 2px white, 0px 0px 0px 4px #15A4A7;
}

.btn.accredo.secondary:active,
.btn.accredo.secondary.active{
  background: #BDE2F3;
  border: 1px solid #15A4A7;
  box-shadow: inset 0 2px 5px 0 rgba(1,0,0,0.20);
}

.btn.accredo.tertiary {
  color: #15A4A7;
  background: transparent;
}

.btn.accredo.tertiary:focus,
.btn.accredo.tertiary:hover:enabled,
.btn.accredo.tertiary.focus, .btn.accredo.tertiary.hover {
  color: darken(#15A4A7, 10%);
  background-color: transparent;
}

.btn.accredo.tertiary:focus,
.btn.accredo.tertiary.focus {
  outline: 0;
  box-shadow: inset 0px 0px 0px 2px #15A4A7;
}



.btn-icon {
  flex: 0 0 auto;
  color: rgba(0, 0, 0, 0.54);
  overflow: visible;
  font-size: 1.5rem;
  text-align: center;
  transition: all 0.2s;
  border-radius: 50%;
  color: inherit;
  border: 0;
  margin: 0;
  cursor: pointer;
  display: inline-flex;
  outline: none;
  padding: 8px;
  align-items: center;
  user-select: none;
  justify-content: center;
  text-decoration: none;
  background-color: transparent;
}

.btn-icon:hover {
  background-color: rgba(0, 0, 0, 0.08);
}

.btn-icon:focus {
  box-shadow: inset 0px 0px 0px 2px #15A4A7;
}

.btn-icon:hover .icon {
  fill: darken(#009BDF, 20%);
}

.icon-wrapper {
  width: 100%;
  display: flex;
  align-items: inherit;
  justify-content: inherit;
  flex: 0 0 auto;
  color: rgba(0, 0, 0, 0.54);
  padding: 4px;
  overflow: visible;
  font-size: 1.5rem;
  text-align: center;
  transition: background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  border-radius: 50%;
}

.icon {
  fill: currentColor;
  width: 1em;
  height: 1em;
  display: inline-block;
  font-size: 24px;
  transition: fill 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  user-select: none;
  flex-shrink: 0;
}
