ul.menu {
  padding: 0;
  margin: 0;
  list-style: none;
}

ul.menu a {
  text-decoration: none;
}

ul.menu a.category {
  position: relative;
  font-size: 19px;
  font-weight: 500;
  padding: 5px 0;
  text-align: center;
	border: 0;
	color: #009BDF;
	border-radius: 4px;
}

ul.menu a.category:hover,
ul.menu a.category:focus,
ul.menu a.category:active {
  background: black;
}

ul.menu a.category:hover + ul.submenu,
ul.menu a.category:focus + ul.submenu,
ul.menu a.category:active + ul.submenu {
	display: block;
}

ul.submenu {
	display: none;
  padding: 8px 0;
  margin: 3px 0 0 0;
  list-style: none;
	border-radius: 4px;
	background: #FFFFFF;
	border: 1px solid #515761;
	box-shadow: 0 2px 8px 0 rgba(0,0,0,0.15);
}

ul.submenu li {
	width: 100%;
	display: table;
}

ul.submenu li a {
	width: 100%;
	font-size: 16px;
	color: #515761;
	display: table-cell;
	vertical-align: middle;
	height: 32px;
	padding: 0 16px;
}

ul.submenu a:hover,
ul.submenu a:focus,
ul.submenu a:active {
  background:#900;
  color:#fff;
}
