.site-alert .close:focus, .site-alert .close.focus {
  box-shadow: 0px 0px 0px 2px white, 0px 0px 0px 4px #515761;
  outline: 0;
}

.site-alert p {
  font-size: 24px;
  font-weight: 300;
  padding: 0;
  width: 400px;
  margin: 6px 0 0 16px;
  float: left;
}

.site-alert .body {
   padding: 16px;
   position: relative;
}









.bx--inline-notification {
    font-family: "ibm-plex-sans", Helvetica Neue, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 0.875rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    background-color: transparent;
    padding: 0.75rem 1rem;
    min-height: 2.5rem;
    color: #5a6872;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.bx--inline-notification__details {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
}

.bx--inline-notification__text-wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin: 0 1rem;
}

.bx--inline-notification__title {
    font-size: 0.875rem;
    color: #152935;
    font-weight: 600;
    margin: 0 0.25rem 0 0;
    line-height: 1.125;
}

.bx--inline-notification__subtitle {
    font-size: 0.875rem;
    line-height: 1.125;
    margin: 0;
}

.bx--inline-notification__icon {
    height: 16px;
    width: 16px;
    min-width: 22px;
}

.bx--inline-notification--error .bx--inline-notification__icon {
    fill: #e0182d;
}

.bx--inline-notification--info .bx--inline-notification__icon {
    fill: #5aaafa;
}

.bx--inline-notification--success .bx--inline-notification__icon {
    fill: #5aa700;
}

.bx--inline-notification--warning {
    border: 1px solid #efc100;
    border-left: 6px solid #efc100;
}


.bx--inline-notification--error {
    border: 1px solid #e0182d;
    border-left: 6px solid #e0182d;
}

.bx--inline-notification--info {
    border: 1px solid #5aaafa;
    border-left: 6px solid #5aaafa;
}

.bx--inline-notification--success {
    border: 1px solid #5aa700;
    border-left: 6px solid #5aa700;
}

.bx--inline-notification--warning .bx--inline-notification__icon {
    fill: #efc100;
}

.bx--inline-notification__close-button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    height: 16px;
    width: 12px;
    position: relative;
}

.bx--inline-notification__close-icon {
    height: 10px;
    width: 10px;
    fill: #5a6872;
    position: absolute;
    top: 3px;
    right: 1px;
}
